import React from "react"
import { Card, CardBody, CardTitle, Badge, Button } from "reactstrap"
import { Link } from "react-router-dom"
import {
  Label,
  Form,
  Input,
  InputGroup,
} from "reactstrap";
const AgentList = () => {
  const leads = [
    {
      id: "customCheck2",
      loginId: "testagent",
      leadCName:"Test Agent",
      phone: "905-492-2356",
      email: "sales@realtyspace.ca",
      link: "#",
      ctype:"rowwarning",
    },
    {
      id: "customCheck2",
      loginId : "francoise",
      leadCName: "Francoise Rautenstrauch",
      phone: "519-569-8399",
      email: "francoise.rautenstrauch@rautenstrauch.com",
      link: "#",
      ctype:""
    },
   
  ]

  return (
    <React.Fragment>
      <Card>
        <CardBody>
        <CardTitle className="mb-4 float-start"></CardTitle>
          <Form className="row gy-2 gx-3 float-end mb15">
         
                      <div className="col-sm-auto">
                        <Label className="visually-hidden" htmlFor="autoSizingInput"></Label>
                        <Input type="text" className="form-control" id="autoSizingInput" placeholder="Search By Name, Phone, Email" />
                      </div>

                     
                      
                      <div className="col-sm-auto">
                        <button type="submit" className="btn btn-primary w-md">Search</button>
                      </div>
                      <div className="col-sm-auto">
                        <Link to="/create-agent"><button type="button" className="btn btn-danger w-md">Add Agent</button></Link>
                      </div>
                    </Form>
                    <div className="clearfix"></div>
          <div className="table-responsive">
            <table className="table align-middle table-nowrap mb-0">
              <thead className="table-light">
                <tr>
                <th className="align-middle">Login Id</th>
                  <th className="align-middle">Name</th>
                  <th className="align-middle">Phone</th>
                  <th className="align-middle">Email</th>
                  

                 
                  <th className="align-middle">View Details</th>
                </tr>
              </thead>
              <tbody>
                {leads.map((lead, key) => (
                  <tr key={"_tr_" + key} className={lead.ctype}>
                    
                    <td>{lead.loginId}</td>
                    <td>{lead.leadCName}</td>
                    <td>{lead.phone} </td>
                    
                    <td>{lead.email}
                     
                    </td>
                    
                   
                    <td>
                    <Link to="#" className="btn-primary waves-effect waves-light btn-sm">View Details</Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </CardBody>
      </Card>
    </React.Fragment>
  )
}

export default AgentList
