import React, { Component } from "react"
import ReactApexChart from "react-apexcharts"

class LeadByLoss extends Component {
  constructor(props) {
    super(props)

    this.state = {
      series: [23, 77, 41],
      options: {
        labels: ["No contact",  "Lost to another agent", "Lost due to Commission rate"],
        colors: ["#c62500", "#d37301", "#cac23d"],
        legend: {
          show: true,
          position: "bottom",
          horizontalAlign: "center",
          verticalAlign: "middle",
          floating: false,
          fontSize: "14px",
          offsetX: 0,
          offsetY: -10,
        },
        responsive: [
          {
            breakpoint: 600,
            options: {
              chart: {
                height: 240,
              },
              legend: {
                show: false,
              },
            },
          },
        ],
      },
    }
  }
  render() {
    return (
      <React.Fragment>
        <ReactApexChart
          options={this.state.options}
          series={this.state.series}
          type="pie"
          height="480"
          className="apex-charts"
        />
      </React.Fragment>
    )
  }
}

export default LeadByLoss
