import React from "react"
import { Card, CardBody, CardTitle, Badge, Button } from "reactstrap"
import { Link } from "react-router-dom"

const AllClients = () => {
  const clients = [
    {
      id: "customCheck2",
      CName: "All Realtors",
      email: "Christmas Party",
      phone: " This years party at Vaughan Centre Presidents",
      ctype:"rowwarning",
      
     
    },
    
   
   
  ]

  return (
    <React.Fragment>
      <Card>
        <CardBody>
         
          <div className="table-responsive">
            <table className="table align-middle table-nowrap mb-0">
              <thead className="table-light">
                <tr>
                  <th style={{ width: "20px" }}>
                    <div className="form-check font-size-16 align-middle">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        id="customCheck1"
                      />
                      <label
                        className="form-check-label"
                        htmlFor="customCheck1"
                      >
                        &nbsp;
                      </label>
                    </div>
                  </th>
                  
                  
                  <th className="align-middle"> Event  </th>
                  <th className="align-middle">Description</th>
                  <th className="align-middle"> Date/Time </th>
                  <th className="align-middle"></th>
                </tr>
              </thead>
              <tbody>
                {clients.map((client, key) => (
                  <tr key={"_tr_" + key} className="">
                    <td>
                      <div className="form-check font-size-16">
                        <input
                          type="checkbox"
                          className="form-check-input"
                          id={client.id}
                        />
                        <label
                          className="form-check-label"
                          htmlFor={client.id}
                        >
                          &nbsp;
                        </label>
                      </div>
                    </td>
                    
                    <td>{client.email}</td>
                    <td>{client.phone}</td>
                    <td>12/20/2019<br />
06:00 PM to 11:00 PM </td>
                    
                   
				
				
                    
               
                   
                    <td>
                        <div className="button-items">
                        <Link to="#" className="btn-primary waves-effect waves-light btn-sm">Edit</Link>&nbsp;
                        <Link to="#" className="btn-secondary waves-effect waves-light btn-sm">Disable</Link>&nbsp;
                        <Link to="#" className="btn-danger waves-effect waves-light btn-sm">Delete</Link>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </CardBody>
      </Card>
    </React.Fragment>
  )
}

export default AllClients
