class LatestLeads extends Component {
  constructor(props) {
    super(props)
    this.state = {
      visible: false,
      modal_standard: false,
      modal_fullscreen: false,
      modal_large: false,
      modal_xlarge: false,
      modal_small: false,
      modal_center: false,
      modal_scroll: false,
      modal_backdrop: false,
    }
    this.tog_standard = this.tog_standard.bind(this)
    this.tog_fullscreen = this.tog_fullscreen.bind(this)
    this.tog_xlarge = this.tog_xlarge.bind(this)
    this.tog_large = this.tog_large.bind(this)
    this.tog_small = this.tog_small.bind(this)
    this.tog_center = this.tog_center.bind(this)
    this.tog_scroll = this.tog_scroll.bind(this)
    this.tog_backdrop = this.tog_backdrop.bind(this)
  }

  tog_standard() {
    this.setState(prevState => ({
      modal_standard: !prevState.modal_standard,
    }))
    this.removeBodyCss()
  }

  removeBodyCss() {
    document.body.classList.add("no_padding")
  }

  tog_fullscreen() {
    this.setState(prevState => ({
      modal_fullscreen: !prevState.modal_fullscreen,
    }))
    this.removeBodyCss()
  }

  tog_backdrop() {
    this.setState(prevState => ({
      modal_backdrop: !prevState.modal_backdrop,
    }))
    this.removeBodyCss()
  }

  tog_large() {
    this.setState(prevState => ({
      modal_large: !prevState.modal_large,
    }))
    this.removeBodyCss()
  }

  tog_xlarge() {
    this.setState(prevState => ({
      modal_xlarge: !prevState.modal_xlarge,
    }))
    this.removeBodyCss()
  }

  tog_small() {
    this.setState(prevState => ({
      modal_small: !prevState.modal_small,
    }))
    this.removeBodyCss()
  }

  tog_center() {
    this.setState(prevState => ({
      modal_center: !prevState.modal_center,
    }))
    this.removeBodyCss()
  }

  tog_scroll() {
    this.setState(prevState => ({
      modal_scroll: !prevState.modal_scroll,
    }))
    this.removeBodyCss()
  }

  show() {
    this.setState({ visible: true })
  }

  hide() {
    this.setState({ visible: false })
  }

  render() {
  return (
    <React.Fragment>
      <Card>
        <CardBody>
        <CardTitle className="mb-4 float-start">Active Leads</CardTitle>
          <Form className="row gy-2 gx-3 float-end">
         
                      <div className="col-sm-auto">
                        <Label className="visually-hidden" htmlFor="autoSizingInput">Name</Label>
                        <Input type="text" className="form-control" id="autoSizingInput" placeholder="Search By Name, Phone, Email" />
                      </div>

                      <div className="col-sm-auto">
                        <Label className="visually-hidden" htmlFor="autoSizingInput">Name</Label>
                        <Input type="text" className="form-control" id="autoSizingInput" placeholder="Last Contact" />
                      </div>
                     
                      <div className="col-sm-auto">
                        <label className="visually-hidden" htmlFor="autoSizingSelect">Preference</label>
                        <select defaultValue="0" className="form-select">
                          <option value="0">Status...</option>
                          <option value="1">No contact</option>
                          <option value="2"> Waiting for response</option>
                          <option value="3"> Ready to meet</option>
                        </select>
                      </div>
                      
                      <div className="col-sm-auto">
                        <button type="submit" className="btn btn-primary w-md">Search</button>
                      </div>
                    </Form>
                    <div className="clearfix"></div>
          <div className="table-responsive">
            <table className="table align-middle table-nowrap mb-0">
              <thead className="table-light">
                <tr>
                  
                  <th className="align-middle">Name</th>
                  <th className="align-middle">Phone</th>
                  <th className="align-middle">Email</th>
                  
                  <th className="align-middle">Last Contact</th>
                  <th className="align-middle">Source</th>
                  <th className="align-middle">Status</th>
                 
                  <th className="align-middle">View Details</th>
                </tr>
              </thead>
              <tbody>
                {leads.map((lead, key) => (
                  <tr key={"_tr_" + key} className={lead.ctype}>
                    
                    <td>
                      <Link to="#" className="text-body fw-bold">
                        {" "}
                        {lead.leadCName}{" "}
                      </Link>{" "}
                    </td>
                    <td>{lead.phone}</td>
                    <td>{lead.email} </td>
                    
                    <td>{lead.lastContact}
                     
                    </td>
                    <td>{lead.type}
                     
                    </td>
                    <td>{lead.leadStatus}
                     
                     </td>
                   
                    <td>
                    <button
                        type="button"
                        className="btn btn-danger"
                        onClick={this.tog_center}
                        data-toggle="modal"
                        data-target=".bs-example-modal-center"
                      >
                        Transfer
                      </button>&nbsp;

                      <Link to="/lead/lead-detail" className="btn btn-primary">Detail</Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <Modal
                        isOpen={this.state.modal_center}
                        toggle={this.tog_center}
                        centered={true}
                      >
                        <div className="modal-header">
                          <h5 className="modal-title mt-0">Transfer</h5>
                          <button
                            type="button"
                            onClick={() =>
                              this.setState({ modal_center: false })
                            }
                            className="close"
                            data-dismiss="modal"
                            aria-label="Close"
                          >
                            <span aria-hidden="true">&times;</span>
                          </button>
                        </div>
                        <div className="modal-body">
                          <FormGroup className="mb15">
                            <Label for="note">Note</Label>
                            <textarea name="" className="form-control"></textarea>
                          </FormGroup>
                          <FormGroup className="mb15">
                            <Label for="agent">Select</Label>
                            <select className="form-control">
                               
                                <option value="">Test Agent</option>
                                <option value="">Mohit Grover</option>

                            </select>
                          </FormGroup>
                          <button type="button" className="btn btn-primary float-end">Save</button>
                        </div>
                      </Modal>
        </CardBody>
      </Card>
    </React.Fragment>
  )
}
}

export default LatestLeads
