import React, { Component } from "react"
import PropTypes from "prop-types"
import MetaTags from 'react-meta-tags';
import { connect } from "react-redux"
import { Link, withRouter } from "react-router-dom"
import * as moment from 'moment';
import { withTranslation } from "react-i18next"
//import FieldsSelector from './../fcomponents/FieldsSelector';
//import FilterList from './fcontainers/FilterList';
import CostLead from './cost-lead'

import {
  Button,
  Col,
  Container,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Row,
  Table,
  UncontrolledDropdown,
  Modal,
  ModalHeader,
  ModalBody,
   Card,
  CardBody,
  CardTitle,
  Label,
  Input,
    FormGroup,
} from "reactstrap"
import {
 
  Form,
 
} from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation"

//Import Breadcrumb
import Breadcrumbs from "../../../components/Common/Breadcrumb"
import { map } from "lodash"

//Import Image
import images from "assets/images"


import {
  getProjects,
  updateProject,
  deleteProject
} from "../../../store/projects/actions"

class ProjectsList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      projects: [],
	  reports: [
        { title: "Active projects", iconClass: "bx-copy-alt", description: "14" },
        {
          title: "Email sent",
          iconClass: "bx-archive-in",
          description: "6",
        },
        
      ],
      modal: false,
    }

    this.handleProjectClick = this.handleProjectClick.bind(this)
    this.toggle = this.toggle.bind(this)
    this.handleValidProjectSubmit = this.handleValidProjectSubmit.bind(this)
    this.handleProjectClicks = this.handleProjectClicks.bind(this)

  }

  componentDidMount() {
    const { projects, onGetProjects } = this.props;
    onGetProjects()
    this.setState({ projects })
  }

  handleProjectClicks = arg => {
    this.setState({ selectedProject: arg })
    this.toggle()
  }

  handleDeleteProject = (project) => {
    const { onDeleteProject } = this.props
    onDeleteProject(project)
  }

  handleProjectClick = arg => {
    const project = arg

    this.setState({
      projects: {
        id: project.id,
        img: project.img,
        name: project.name,
        description: project.description,
        status: project.status,
        color: project.color,
        dueDate: project.dueDate,
        team: project.team
      }
	  
    })

    this.toggle()
  }

  toggle() {
    this.setState(prevState => ({
      modal: !prevState.modal,
    }))
  }

  /**
   * Handling submit user on user form
   */
  handleValidProjectSubmit = (e, values) => {

    const { onAddNewProject, onUpdateProject } = this.props
    const { isEdit, projects, selectedProject } = this.state

    if (isEdit) {
      const updateProject = {
        id: projects.id,
        img: values.img,
        name: values.name,
        description: values.description,
        status: values.status,
        color: values.color,
        dueDate: values.dueDate,
        team: values.team
      }

      // update user
      onUpdateProject(updateProject)
    } else {

      const newProject = {
        id: Math.floor(Math.random() * (30 - 20)) + 20,
        name: values["name"],
        description: values["description"],
        status: values["status"],
        color: values["color"],
        dueDate: values["dueDate"],
        team: values["team"]
      }
      // save new user
      onAddNewProject(newProject)
    }
    this.setState({ selectedProject: null })
    this.toggle()
  }

  handleValidDate = (date) => {
    const date1 = moment(new Date(date)).format('DD MMM Y');
    return date1;
  }

  render() {
    const { projects } = this.props
    const { isEdit } = this.state

    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Projects | Realty Space</title>
          </MetaTags>
          <Container fluid>
            {/* Render Breadcrumbs */}
            <Breadcrumbs
              title={this.props.t("Projects")}
              breadcrumbItem={this.props.t("Projects")}
            />
           <Row className="mb20">
		   	
	
             <Col lg="12">
               <div className="clearfix">
               <Link to="#" className="btn btn-danger float-end"><i className="bx bx-plus"></i> {this.props.t("Add New Project")}</Link>
             </div>
             </Col>
           </Row>
		   <Row>
              
            </Row>
            
            <Row>
              <Col lg="12">
                <div className="">
                <Form className="row gy-2 gx-3 float-end">
         
         <div className="col-sm-auto">
           <Label className="visually-hidden" htmlFor="autoSizingInput">Name</Label>
           <Input type="text" className="form-control" id="autoSizingInput" placeholder="Completion Year" />
         </div>

         <div className="col-sm-auto">
           <Label className="visually-hidden" htmlFor="autoSizingInput">Name</Label>
           <Input type="text" className="form-control" id="autoSizingInput" placeholder="Name of City" />
         </div>

         <div className="col-sm-auto">
           <Label className="visually-hidden" htmlFor="autoSizingInput">Name</Label>
           <Input type="text" className="form-control" id="autoSizingInput" placeholder="Starting Price From" />
         </div>
        
         <div className="col-sm-auto">
                        <label className="visually-hidden" htmlFor="autoSizingSelect">Status</label>
                        <select defaultValue="0" className="form-select">
                          <option value="0">Status...</option>
                          <option value="1">Open</option>
                          <option value="2"> Closed</option>
                          <option value="3"> Upcoming</option>
                        </select>
                      </div>
         
         <div className="col-sm-auto">
           <button type="submit" className="btn btn-primary w-md">Search</button>
         </div>
       </Form>
       <div className="clearfix"></div>
                  <div className="table-responsive">
                    <Table className="project-list-table table-nowrap align-middle table-borderless">
                      <thead>
                        <tr>
                          
                          <th scope="col">{this.props.t("Project Name")}</th>
                         
                          <th scope="col">{this.props.t("#SRR")}</th>
						              <th scope="col">{this.props.t("#Clients")}</th>
                          
                          <th scope="col">{this.props.t("Action")}</th>
                        </tr>
                      </thead>
                      <tbody>
                       
                          <tr>
                            
                            <td>
                              <h5 className="text-truncate font-size-14">
                                <Link
                                  to={`/pre-construction/project-detail`}
                                  className="text-dark"
                                >
                                    Liberty Market Tower 
                                </Link>
                              </h5>
                              <p className="text-muted mb-0">
                              Toronto, ON
                              </p>
                            </td>

                           
                            <td>
                              50
                           
                             
                            </td>
                            <td>60</td>
 							             
                            <td>
                            <Link to="/project/project-detail" className="btn btn-primary">Detail</Link>
                            </td>
                          </tr>

                          <tr>
                            
                            <td>
                              <h5 className="text-truncate font-size-14">
                                <Link
                                  to={`/pre-construction/project-detail`}
                                  className="text-dark"
                                >
                                      The Dupont 
                                </Link>
                              </h5>
                              <p className="text-muted mb-0">
                              Toronto, ON
                              </p>
                            </td>
                                      
                         
                            <td>
                              
                            20
                             
                            </td>
                            <td>35</td>
 							
                            <td>
                             <Link to="#" className="btn btn-primary">Detail</Link>
                            </td>
                          </tr>
                      
                      </tbody>
                    </Table>
                    
                  </div>
                </div>
              </Col>
            </Row>

    
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

ProjectsList.propTypes = {
  projects: PropTypes.array,
  className: PropTypes.any,
  onGetProjects: PropTypes.func,
  onDeleteProject: PropTypes.func,
  onUpdateProject: PropTypes.func,
  onAddNewProject: PropTypes.func,
  t: PropTypes.any
}

const mapStateToProps = ({ projects }) => ({
  projects: projects.projects,
})

const mapDispatchToProps = dispatch => ({
  onGetProjects: () => dispatch(getProjects()),
  onUpdateProject: project => dispatch(updateProject(project)),
  onDeleteProject: project => dispatch(deleteProject(project)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(withTranslation()(ProjectsList)))