import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
import AgentList from "./agentlist"
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle
} from "reactstrap"

class agentDashoard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Broker Agent | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Broker")}
              breadcrumbItem={this.props.t("Agent")}
            />
           
            <Row>
            <Col lg="12">
              <AgentList />
            </Col>
          </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
agentDashoard.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(agentDashoard))
