import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"

import "flatpickr/dist/themes/material_blue.css"
import Flatpickr from "react-flatpickr"
import {
  Container,
  Row,
  Col,
  Button,
  ButtonDropdown,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Form, FormGroup, Input, InputGroup, Label
} from "reactstrap"
import AllLeads from "./Allleads"
class LeadList extends Component {
  constructor(props) {
    super(props)
    this.state = {
     
  }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>All Leads | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Lead")}
              breadcrumbItem={this.props.t("All Leads")}
            />
            
            <Row>
            <Col lg="12">
             <Row>
                 <Col md="4">
                 <form className="app-search d-none d-lg-block">
                <div className="position-relative">
                  <input
                    type="text"
                    className="form-control"
                    placeholder={this.props.t("Search by Name, Phone, Email") + "..."}
                  />
                  <span className="bx bx-search-alt"></span>
                </div>
              </form>
                 </Col>
                 <Col md="8">
                 

                <div
                      className="btn-group mr-1 mt-2"
                      role="group"
                      aria-label="button group"
                    >
                         <ButtonDropdown
                        isOpen={this.state.drp_link}
                        toggle={() =>
                          this.setState({ drp_link: !this.state.drp_link })
                        }
                      >
                        <DropdownToggle caret color="secondary">
                        Lead Status <i className="mdi mdi-chevron-down"></i>
                        </DropdownToggle>
                        <DropdownMenu>
                          <DropdownItem>Open-New</DropdownItem>
                          <DropdownItem>Open-Follow-up</DropdownItem>
                        </DropdownMenu>
                      </ButtonDropdown>
                </div>

                <div
                      className="btn-group mr-1 mt-2"
                      role="group"
                      aria-label="button group"
                    >
                         <ButtonDropdown
                        isOpen={this.state.drp_link}
                        toggle={() =>
                          this.setState({ drp_link: !this.state.drp_link })
                        }
                      >
                        <DropdownToggle caret color="secondary">
                        Lead Classification <i className="mdi mdi-chevron-down"></i>
                        </DropdownToggle>
                        <DropdownMenu>
                          <DropdownItem>Hot</DropdownItem>
                          <DropdownItem>Warm</DropdownItem>
                          <DropdownItem>Cold</DropdownItem>
                        </DropdownMenu>
                      </ButtonDropdown>
                </div>
                <div
                      className="btn-group mr-1 mt-2"
                      role="group"
                      aria-label="button group"
                    >           
                <FormGroup className="">
                        
                        <InputGroup>
                          <Flatpickr
                            className="form-control d-block"
                            placeholder="Search Create Date"
                            options={{
                              altInput: true,
                              altFormat: "F j, Y",
                              dateFormat: "Y-m-d"
                            }}
                          />
                        </InputGroup>
                      </FormGroup>
                    </div>
                 </Col>
             </Row>
              <AllLeads />
            </Col>
          </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
LeadList.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(LeadList))
